import React from 'react'
import userService from '../services/user'


class Login extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            name: '',
            username: '',
            password: ''
        } 
    }


    propertyChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

     register = (e) => {
         //e.preventDefault()

         const newUser = {
             name: this.state.name,
             username: this.state.username,
             password: this.state.password
         }

         if(newUser.name === '' || newUser.username === '' || newUser.password === ''){
             e.preventDefault()
             window.confirm('PLEASE ENTERS YOUR DETAILS')
         } else {
             userService
                 .creatUser(newUser)
                 .then(() => {
                     this.setState({
                         name: '',
                         username: '',
                         password: ''
                     })
                 })
         }
     }

     render (){
         return(
             <div className='login-div'>
                 <h1>Log into app</h1>
                 <form className='login-form' onSubmit={this.props.submit}>
                    Username: <input className='boxes' name='username' onChange={this.props.handleChange} type='text'/><br />
                    Password: <input className='boxes' name='password' onChange={this.props.handleChange} type='password'/><br />
                     <button>Login</button>
                 </form>
                 <h1>Register as a user</h1>
                 <form onSubmit={this.register}>
                    Name: <input className='boxes'  name='name'  onChange={this.propertyChange} type='text'/>
                    Username: <input className='boxes' name='username'  onChange={this.propertyChange} type='text'/><br />
                    Password: <input className='boxes' name='password'  onChange={this.propertyChange} type='password'/><br />
                    
                     <button>Register</button>
                 </form>
             </div>
         )}
}

export default Login

