import React from 'react'
import staffService from '../services/staff'
import Login from './Login'
import loginService from '../services/login'
import { Table } from 'semantic-ui-react'

class Workers extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            workers: [],
            name: '',
            address: '',
            title: '',
            password: '',
            user: '',
            username: ''
        }
        this.handleChange = this.handleChange.bind(this)
        this.addNewWorker = this.addNewWorker.bind(this)
        this.login = this.login.bind(this)
    }
    componentDidMount() {
        staffService
            .getAll()
            .then(staff => {
                this.setState({
                    workers: staff
                })
            })
    }

    login = async (e) =>{
        try {
            e.preventDefault()
            //console.log(this.state.username, this.state.password)

            const user = await loginService.login({
                username: this.state.username,
                password: this.state.password
            })
            
            window.localStorage.setItem('currentUser', JSON.stringify(user))
            staffService.setToken(user.token)
            this.setState({
                username: '',
                password: '',
                user
            })
        } catch(exception){
            alert('wrong credentials')
            
        }
    }


    handleChange(e){
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    logout =  (e) => {
        window.localStorage.clear()
        window.location.reload(true)
    }
    deleteItem(id) {
        return() => {
            //the right worker to delete
            const toTakeDown = this.state.workers.find(found => found.Id === id)
                
            //the rest of the workers
            const reMainingWorkers = this.state.workers.find(found => found.Id !== id)
            staffService
                .takeDown(id, toTakeDown)
                .then( res => {
                    console.log('res: ',res)
                    this.setState({
                        workers: this.state.workers.push(reMainingWorkers)
                    })
                })
            window.location.reload(true)
        }
    }
     
      
    addNewWorker(e) {
        const newStaff =  {
            name: this.state.name,
            address: this.state.address,
            title: this.state.title
        }
        if(newStaff.name === '' || newStaff.address === '' || newStaff.title === ''){
            e.preventDefault()
            window.confirm('DETAILS FIELD CANNOT BE EMPTY')
        } else {
            staffService
                .addNew(newStaff)
                .then(staff => {
                    this.setState({
                        workers: this.state.workers.concat(staff),
                        name: '',
                        address: '',
                        title: ''
                    })
                })
        }
    }

    render(){
        if(window.localStorage.getItem('currentUser') === null) {
            return (
                <div>
                    {<Login 
                        handleChange={this.handleChange}
                        submit={this.login}
                         
                    />}
                </div>
            )
        }

        return (
            <div className='login-div'>
                <button onClick={this.logout}>Logout</button>
                <div>
                    <h2>ADD NEW WORKER</h2>
                    <form onSubmit={this.addNewWorker}>
                        Name: <input className='boxes' type="text" name="name" onChange={this.handleChange}/><br />
                        Address: <input className='boxes' type="text" name="address" onChange={this.handleChange}/><br />
                        Title: <input className='boxes' type="title" name="title" onChange={this.handleChange}/><br />
                        <button>ADD</button>
                    </form>

                </div>
                <h2>STAFF</h2>
                <Table striped celled>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell>NAME</Table.HeaderCell>
                            <Table.HeaderCell>TITLE</Table.HeaderCell>
                            <Table.HeaderCell>ADDRESS</Table.HeaderCell>
                            <Table.HeaderCell></Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>
                    <Table.Body>
                        {this.state.workers.map(m => 
                            <Table.Row key={m.Id}>
                                <Table.Cell> {m.Name}</Table.Cell>
                                <Table.Cell>{m.Title}</Table.Cell>
                                <Table.Cell>{m.Address}</Table.Cell>
                                <Table.Cell>
                                    <button onClick={this.deleteItem(m.Id)}>
                                        Delete
                                    </button>
                                </Table.Cell>
                    
                            </Table.Row>)
                        }
                    </Table.Body>
                </Table>
            </div>
        )
    }
}

export default Workers