import axios from 'axios'
const baseUrl = '/api/staff'

//global member variable
let token = null

const getAll =  () => {
    const request = axios.get(baseUrl)
    return request.then(res => res.data)
}

const addNew = async (obj) => {
    const newWorker = await axios.post(baseUrl, obj)
    return newWorker.data
}

const takeDown = async (id, blog) => {
    const res =await axios.delete(`${baseUrl}/${id}`,blog)
    return res.data
}

const setToken = async (inComingToken) => {
    token = `bearer ${inComingToken}`
}

export default { getAll ,addNew, takeDown, setToken }